@import "./src/_variables";

.deleting-confirm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 611px;
  height: 155px;

  box-sizing: border-box;

  padding: 24px 44px;

  background: #F4F4F4;
  box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);

  border-radius: 8px;

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;

    border-radius: 8px;
    border: none;
    outline: none;
    width: 100%;
    height: 40px;

    &:first-child {
      margin-right: 15px;
    }

    &--delete {
      background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
      color: #fff;
    }

    &--cancel {
      position: relative;
      background: $background-gradient-blue;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px;
        border: 1px solid transparent;
        background: $background-gradient-blue border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }
    }

    &--wrapper {
      display: flex;
      width: 100%;
    }
  }

  &__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    margin: 0;
    margin-bottom: 18px;
  }

  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    margin: 0;
    margin-bottom: 9px;
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 20;

    position: fixed;
    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    background: rgba(0, 0, 0, 0.3);
  }
}