@import '../../../../_variables';

.print-data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  &__buttons {
    display: flex;
    gap: 17px;
  }

  &__button {
    width: 100%;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
  }

  &__cancel {
    padding: 10px 0;
    border-radius: 8px;
    color: #3265D0;
    position: relative;
    border: none;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 8px;
      border: 1px solid transparent;
      background: $background-gradient-blue border-box;
      -webkit-mask: linear-gradient(#fff 0 0) padding-box,
      linear-gradient(#fff 0 0);
      -webkit-mask-composite: destination-out;
      mask-composite: exclude;
    }
  }

  &__print {
    color: #f4f4f4;
    background: $background-gradient-blue;
    border-radius: 8px;
    border: none;
  }

  &__container {
    position: fixed;

    background: rgba(0, 0, 0, 0.3);

    width: 100vw;
    height: 100vh;

    z-index: 15;
  }

  &__left,
  &__right {
    width: 507px;
    height: 790px;
    background-color: #f4f4f4;
  }

  &__left {
    box-sizing: border-box;
    margin-right: 16px;
    border-radius: 5px;

    padding: 39px 44px;

    &-title {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 35px;
      line-height: 43px;

      margin: 0;
      margin-bottom: 16px;
    }

    &-item {

      &--info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        height: 40px;
        background-color: #fff;
        border-radius: 8px;

        padding: 10px;
        padding-right: 16px;
        margin-bottom: 12px;
      }

      &--title {
        display: flex;
        align-items: center;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        color: #919090;

        margin-bottom: 4px;
      }
    }
  }

  &__right {
    width: 563px;
  }

  &__svg {
    margin-right: 8px;
  }
}