@import "./src/_variables";

.export-data {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;

  &__button {
    box-sizing: border-box;
    padding: 10px 40px;

    color: #fff;

    border: none;
    background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
    border-radius: 8px;

    &:disabled {
      background: $background-disabled-button;
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    box-sizing: border-box;
    padding: 44px;
    width: 612px;
    height: 284px;

    background: #F4F4F4;

    box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
  }

  &__body {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;

    &-title {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      margin: 0;
      margin-bottom: 16px;
    }

    &-counter {

      &--num {
        background: $gradient-green;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
    }
  }

  &__form {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: flex-end;

    &-input {
      display: block;
      width: 100%;

      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;

      box-sizing: border-box;
      border: none;
      outline: none;
      background: #FFFFFF;
      border-radius: 8px;

      margin-bottom: 8px;
      padding: 10px 12px;
    }
  }

  &--close {
    position: absolute;
    right: 11px;
    top: 11px;
  }
}