@import "./src/_variables";

.details {
  position: fixed;

  box-sizing: border-box;

  top: 36vh;
  right: 9vw;

  width: 470px;
  //height: 461px;

  background-color: #f4f4f4;

  z-index: 201;

  box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);
  border-radius: 8px;

  padding: 24px 44px;
  border: none;

  &__body-buttons {
    display: flex;
    width: inherit;
    height: min-content;
    align-items: center;
    gap: 11px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44%;
    height: 27px;

    border: none;
    outline: none;
    border-radius: 6px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;

    position: relative;

    &--clear {
      background: $background-gradient-blue;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px;
        border: 1px solid transparent;
        background: $background-gradient-blue border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }
    }

    &--save {
      color: #f4f4f4;
      background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
    }

    &--delete {
      width: 28px;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px;
        border: 1px solid transparent;
        background: $gradient-red border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }
    }
  }

  &__input {
    box-sizing: border-box;
    display: block;
    width: 100%;

    background: #FFFFFF;
    border-radius: 6px;

    border: none;
    outline: none;

    padding: 8px 10px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: .8rem;
    line-height: 12px;
    color: #000;

    &::placeholder {
      font-weight: 300;
      color: #919090;
    }
  }

  &__body-first-part {
    margin-bottom: 10px;
  }

  &__body-second-part {
    margin-bottom: 10px;
  }

  &__body-third-part {
    display: flex;
    gap: 11px;
    margin-bottom: 10px;
  }

  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 10px;
  }
}