@import "./src/_variables";

.save-filters {
  width: 612px;
  height: 363px;

  box-sizing: border-box;

  background: #F4F4F4;

  box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);
  border-radius: 8px;

  padding: 44px;

  position: relative;

  &__buttons-wrapper {
    display: flex;
    gap: 10px;
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 10;

    position: fixed;
    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    background: rgba(0, 0, 0, 0.3);
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__first-section {
    margin-bottom: 15px;

    &--title {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;

      margin-bottom: 15px;
    }
  }

  &__save-button {
    background: $background-gradient-blue;

    border: none;
    border-radius: 8px;

    padding: 10px 40px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;

    color: #F4F4F4;

    margin-top: 15px;

    &:disabled {
      background: #B4B4B4;
      cursor: no-drop;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;

    padding: 10px 17px 10px 12px;

    background: #FFFFFF;
    border-radius: 8px;

    margin-bottom: 15px;

    position: relative;
  }

  &__name-input {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    background: #FFFFFF;
    border-radius: 8px;

    width: 100%;
    box-sizing: border-box;

    padding: 10px 12px 10px 37px;

    outline: none;
    border: none;
  }

  &__button {
    border: none;
    border-radius: 8px;

    padding: 10px 40px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    width: 100%;

    &--accept {
      color: #f4f4f4;
      background: $background-gradient-blue;
      transition: all 2s;

      &:disabled {
        background: #b4b4b4;
        cursor: no-drop;
      }
    }

    &--details {
      position: relative;
      background: $background-gradient-blue;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px;
        border: 1px solid transparent;
        background: $background-gradient-blue border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }

      &:disabled {
        cursor: no-drop;
        
        background: #B4B4B4;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border-radius: 8px;
          border: 1px solid transparent;
          background: #b4b4b4 border-box;
          -webkit-mask: linear-gradient(#fff 0 0) padding-box,
          linear-gradient(#fff 0 0);
          -webkit-mask-composite: destination-out;
          mask-composite: exclude;
        }
      }
    }

    &--delete {
      position: relative;
      background: $gradient-red;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px;
        border: 1px solid transparent;
        background: $gradient-red border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }

      &:disabled {
        cursor: no-drop;

        background: #B4B4B4;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border-radius: 8px;
          border: 1px solid transparent;
          background: #b4b4b4 border-box;
          -webkit-mask: linear-gradient(#fff 0 0) padding-box,
          linear-gradient(#fff 0 0);
          -webkit-mask-composite: destination-out;
          mask-composite: exclude;
        }
      }
    }
  }

  &--close {
    position: absolute;
    top: 11px;
    right: 11px;
  }

  &--message {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 12px;
  }
}
