@import "./src/_variables";

.delete-category {
  position: relative;
  box-sizing: border-box;

  padding: 44px;

  width: 612px;
  height: 349px;

  background: #F4F4F4;

  box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);
  border-radius: 8px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;

  &__button {
    color: #f4f4f4;
    background: $background-gradient-blue;
    border: none;
    outline: none;

    border-radius: 8px;

    padding: 10px 40px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    &--wrapper {
      display: flex;
      justify-content: flex-end;
    }
  }

  &__category {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    background: #fff;
    border-radius: 8px;
    padding: 10px 15px 10px 12px;
    box-sizing: border-box;
    margin-bottom: 18px;
  }

  &__info {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 9px;
  }

  &__counter {
    margin: 0;
    margin-bottom: 18px;

    &--green {
      background: $gradient-green;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
  }

  &__title {
    text-align: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 9px;
  }

  &__name {
    text-align: center;
    margin: 0;
    margin-bottom: 43px;
  }

  &__close {
    position: absolute;
    top: 11px;
    right: 11px;
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.3);
    z-index: 11;
  }
}