@import "./src/_variables";

.new-counterpart {
  box-sizing: border-box;
  padding: 26px 43px 0 33px;

  &__nav {
    display: flex;
    justify-content: flex-end;

    &-list {
      display: flex;
      width: 100%;
      gap: 5%;
      justify-content: flex-end;
      list-style: none;
      margin: 0;
      padding: 0;
    }

    &-item {
      position: relative;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      white-space: nowrap;
    }
  }

  &__big-plus {
    margin-left: 8px;
  }

  &__second-group {
    display: flex;
    width: 100%;
    gap: 36px;
    margin-bottom: 10px;

    & ~ .gap-8 {
      gap: 8px;
      justify-content: space-between;
    }
  }

  &__section-main {
    display: flex;
    gap: 34px;
  }

  &__requisites {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    color: #000000;
    margin: 0;
    margin-bottom: 12px;
  }

  &__label {
    display: flex;
    gap: 10px;
  }

  &__input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    background: #FFFFFF;
    border-radius: 8px;
    height: 40px;
    display: inline-block;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;

    margin-bottom: 12px;
    margin-top: 4px;

    position: relative;


    @media (max-width: 1550px) {
      height: 30px;
      font-size: 13px;
      //line-height: 15px;
    }

    @media (max-height: 800px) {
      height: 30px;
    }
  }

  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 43px;
    margin: 0;
    margin-bottom: 4px;
  }

  &--left-part {
    width: 27%;
  }

  &__id {
    display: flex;
    align-items: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 9px;
    color: #919090;

    &--id {
      color: #000;
      margin-right: 4px;
    }
  }

  &__button {
    border: none;
    outline: none;
    color: #f4f4f4;
    padding: 10px 40px;
    background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
    border-radius: 8px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
  }

  &--right-part {
    width: 100%;
    position: relative;
  }

  &__checkboxes {
    display: flex;
    height: 40px;

    background: #E8E8E8;
    border-radius: 8px;

    margin-bottom: 9px;

    &--label {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50%;
      position: relative;
    }

    &--left, &--right {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      color: #B4B4B4;
      white-space: nowrap;
    }

    #left, #right {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }

    #left {
      &:checked ~ .new-counterpart__checkboxes--left {
        background: #fff;
        border-radius: 8px 40px 40px 8px;
        color: #2152BB;
      }

      &:checked ~ .border {
        transition: all .35s;
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 8px 40px 40px 8px;
        border: 1px solid transparent;
        background: $background-gradient-blue border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }
    }

    #right {
      &:checked ~ .new-counterpart__checkboxes--right {
        background: #fff;
        border-radius: 40px 8px 8px 40px;
        color: #2152BB;
      }

      &:checked ~ .border {
        transition: all .35s;
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 40px 8px 8px 40px;
        border: 1px solid transparent;
        background: $background-gradient-blue border-box;
        -webkit-mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
        -webkit-mask-composite: destination-out;
        mask-composite: exclude;
      }
    }
  }
}

.flex-end {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}