  &__left-side-first-group {
    display: flex;
    gap: 24px;

    &--element {
      width: 100%;
    }

    article {
      margin: 0;
      margin-bottom: 4px;
      color: #919090;
    }

    p {
      margin: 0;
    }
  }

  &__left-side-fourth-group {

    .import-data__informer {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-radius: 8px;
      padding: 10px;
      background-color: #fff;
      margin-bottom: 12px;
      width: 100%;

      &:last-child {
        width: 60%
      }
    }

    &--wrapper-info {
      display: flex;
      gap: 8px;
    }

    article {
      margin: 0;
      margin-bottom: 4px;
      color: #919090;
    }

    p {
      margin: 0;
    }
  }

  &__informer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    padding: 10px;
    background-color: #fff;
    margin-bottom: 12px;

    position: relative;
  }

  &__button {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    box-sizing: border-box;
    padding: 10px 40px;

    color: #fff;

    border: none;
    background: $background-gradient-blue;
    border-radius: 8px;

    &:disabled {
      background: $background-disabled-button;
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    box-sizing: border-box;
    padding: 44px;

    width: 1090px;
    height: 845px;

    background: #F4F4F4;

    box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);
    border-radius: 8px;

    &-button {
      display: flex;
      justify-content: flex-end;

      position: absolute;
      bottom: 0;
      right: 0;
    }

    &-result {
      position: relative;

      box-sizing: border-box;
      padding: 44px;

      width: 642px;
      height: 168px;

      background: #F4F4F4;

      box-shadow: -2px -2px 130px -15px rgba(0, 0, 0, 0.25);
      border-radius: 8px;

      &-text {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;

        margin: 0;
        margin-bottom: 16px;
      }

      &-body {
        display: flex;
        justify-content: space-between;

        &-paragraph {
          margin: 0;
          margin-bottom: 4px;

          &--wrapper {
            width: 100%;
          }

          &--blue {
            background: $background-gradient-blue;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;

            white-space: nowrap;

            cursor: pointer;
          }

          &--red {
            background: $gradient-red;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
          }

          &--green {
            background: $gradient-green;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;
          }
        }
      }
    }
  }

  &__header {
    width: 100%;

    &-info {
      margin: 0;
      margin-bottom: 4px;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 43px;
    margin: 0;
    margin-bottom: 15px;
  }

  &__file {

    &-details {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 16px;
    }

    &-name {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      color: #000000;
      box-sizing: border-box;

      height: 40px;

      padding: 10px;
      width: 93%;
      background: #fff;
      border-radius: 8px;
    }
  }

  &__label {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;

    color: #919090;

    margin-bottom: 4px;
  }

  &__body {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: flex-start;

    &-left-side {
      width: 100%;
      height: 100%;
    }

    &-right-side {
      width: 100%;
      height: 100%;
      position: relative;
    }

    &-content--wrapper {
      display: flex;
      width: 100%;
      height: 100%;
      gap: 42px;
    }

    &-counter {

      &--num {
        background: $gradient-green;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
    }
  }

  &--close {
    position: absolute;
    right: 11px;
    top: 11px;
  }
}